* {
  box-sizing: border-box;
}

html,
body,
#root {
  padding: 0;
  margin: 0;
  height: 100vh;
  overflow: hidden
}

body {
  font-family: 'Source Sans Pro', 'Trebuchet MS', 'Lucida Grande', 'Bitstream Vera Sans', 'Helvetica Neue', sans-serif;
  font-size: 20px;
  margin: 0;
  text-align: center;
  background-color: #2d3436;
  color: #ecf0f1;
}

body>div {
  padding: 0;
  margin: 0;
  height: 100vh;
  overflow: hidden
}

.hidden {
  display: none !important;
}

.main--container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.main--calendar {
  overflow: hidden;
  display: flex;
  /* flex: 1; */
}

.main--toolbar {
  display: flex;
  background-color: #2c3e50;
  padding: 0;
  justify-content: space-between;
  height: 40px;
  position: relative;
}

.main--month-title {
  cursor: pointer;
  position: absolute;
  width: 100%;
  height: 100%;
}

.main--month-title h2 {
  margin: 0;
  padding: 5px 0;
  font-size: 22px;
  white-space: nowrap;
}

.main--navigatiors-button,
.main--selector-select {
  height: 100%;
  border: 0;
  background-color: transparent;
  color: #ecf0f1;
  font-size: 15px;
  cursor: pointer;
  vertical-align: top;
}

.main--navigatiors-button:hover,
.main--selector-select:hover {
  background-color: #34495e;
}

.main--navigatiors-reload {
  font-size: 1.3em;
  line-height: 100%;
}

.main--navigatiors-reload span {
  display: block;
  padding-left: 4px;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  -webkit-animation-name: none;
          animation-name: none;
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.main--navigatiors-reload.loop span {
  -webkit-animation-name: loop;
          animation-name: loop;
}

.main--navigatiors-button {
  padding: 0 8px;
  text-transform: uppercase;
}

.main--paginators,
.main--selector {
  z-index: 1;
}

.main--selector-select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.main--message {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fun2 .calendar--event {
  background-color: #e30404 !important;
  overflow: visible;
  -webkit-animation: 4s singing infinite alternate linear;
          animation: 4s singing infinite alternate linear;
}

.fun2 .calendar--week-content .calendar--day:nth-child(odd) .calendar--event {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.fun2 .calendar--week-content .calendar--day:nth-child(even) .calendar--event {
  -webkit-animation-delay: 0;
          animation-delay: 0;
}

.fun2 .calendar--event::after {
  content: '';
  background-image: url("http://en.shapki-ushanki.ru/img/ushanka.png");
  width: 100%;
  max-width: 150px;
  height: 100px;
  display: block;
  background-size: cover;
  position: absolute;
  top: -80px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.fun2 .calendar--event::before {
  content: '';
  background-image: url("http://gifimage.net/wp-content/uploads/2017/08/mouth-gif-21.gif");
  width: 50%;
  max-width: 50px;
  height: 50px;
  display: block;
  background-size: cover;
  position: absolute;
  bottom: 10%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  mix-blend-mode: multiply;
}

.fun .calendar--week-content .calendar--day:nth-child(odd) .calendar--event:nth-child(odd) {
  -webkit-animation: linear infinite 4s 0.6s fun alternate !important;
          animation: linear infinite 4s 0.6s fun alternate !important;
}

.fun .calendar--week-content .calendar--day:nth-child(odd) .calendar--event:nth-child(even) {
  -webkit-animation: linear infinite 4s 0.2s fun alternate !important;
          animation: linear infinite 4s 0.2s fun alternate !important;
}

.fun .calendar--week-content .calendar--day:nth-child(even) .calendar--event:nth-child(odd) {
  animation: linear infinite 4s fun alternate-reverse !important;
}

.fun .calendar--week-content .calendar--day:nth-child(even) .calendar--event:nth-child(even) {
  animation: linear infinite 4s 0.5s fun alternate-reverse !important;
}

.fun .main--navigatiors-reload span {
  animation: linear infinite 4s 0.3s fun alternate-reverse !important;
}

@-webkit-keyframes loop {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes loop {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes fun {
  0% {
    -webkit-transform: rotate(0) scale(0.8);
            transform: rotate(0) scale(0.8);
  }

  50% {
    -webkit-transform: rotate(20deg) scale(1.2);
            transform: rotate(20deg) scale(1.2);
  }

  100% {
    -webkit-transform: rotate(-720deg) scale(0.8);
            transform: rotate(-720deg) scale(0.8);
  }
}

@keyframes fun {
  0% {
    -webkit-transform: rotate(0) scale(0.8);
            transform: rotate(0) scale(0.8);
  }

  50% {
    -webkit-transform: rotate(20deg) scale(1.2);
            transform: rotate(20deg) scale(1.2);
  }

  100% {
    -webkit-transform: rotate(-720deg) scale(0.8);
            transform: rotate(-720deg) scale(0.8);
  }
}

@-webkit-keyframes singing {
  0% {
    -webkit-transform: translatey(0) scale(1);
            transform: translatey(0) scale(1);
  }

  80% {
    -webkit-transform: translatey(-15px) scale(1.05) rotate(1deg);
            transform: translatey(-15px) scale(1.05) rotate(1deg);
  }

  100% {
    -webkit-transform: translatey(-20px) scale(1.1) rotate(-1deg);
            transform: translatey(-20px) scale(1.1) rotate(-1deg);
  }
}

@keyframes singing {
  0% {
    -webkit-transform: translatey(0) scale(1);
            transform: translatey(0) scale(1);
  }

  80% {
    -webkit-transform: translatey(-15px) scale(1.05) rotate(1deg);
            transform: translatey(-15px) scale(1.05) rotate(1deg);
  }

  100% {
    -webkit-transform: translatey(-20px) scale(1.1) rotate(-1deg);
            transform: translatey(-20px) scale(1.1) rotate(-1deg);
  }
}